<template>
  <div class="container p-4">
    <form @submit.prevent="formSubmit">
      <div class="card m-4">
        <div class="card-header">Mon Profile</div>
        <div class="card-body">
          <div class="text-center text-warning" v-if="errorMessage">
            {{ errorMessage }}
            <hr />
            <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
          </div>
          <loading-compoenent v-else-if="isLoading" />
          <div v-else>
            <div class="row">
              <div class="col">
                <div class="form-group py-1">
                  <label for="input-live">First Name: *</label>
                  <input
                    type="text"
                    v-model="document.first_name"
                    class="form-control"
                    trim
                    require
                  />
                </div>
              </div>
              <div class="col">
                <div class="form-group py-1">
                  <label for="input-live">Last Name: *</label>
                  <input type="text" v-model="document.last_name" class="form-control" trim require />
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col">
                <div class="form-group">
                  <label>State: *</label>
                  <select-component v-model="document.state" :options="states" trim required />
                </div>
              </div>
              <div class="col">
                <div class="form-group">
                  <label>City: *</label>
                  <select-component
                    v-model="document.city"
                    :options="cities_filtred"
                    trim
                    required
                  />
                </div>
              </div>
            </div>
            <div class="row">
              <div class="col">
                <div class="form-group py-1">
                  <label for="input-live">Email: *</label>
                  <input type="text" v-model="document.email" class="form-control" trim require />
                </div>
              </div>
              <div class="col">
                <div class="form-group py-1">
                  <label for="input-live">Mobile: *</label>
                  <input type="text" v-model="document.mobile" class="form-control" trim require />
                </div>
              </div>
            </div>
            <div role="group" class="mb-2">
              <label for="input-live">Kind:</label>
              <select-component v-model="document.kind" :options="kinds" trim required />
            </div>
          </div>
        </div>
        <div class="card-footer" v-if="!errorMessage && !isLoading">
          <div class="d-grid p-1">
            <button class="btn btn-primary btn-lg" type="submit">
              <i class="bi-person me-2" />Suivant
            </button>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { db, FF_FieldValue } from "../firebase";
import algeria_cities from "../assets/algeria_cities.js";

export default {
  name: "inscriptionDistributeurComponent",
  components: {},
  data() {
    return {
      kinds: ["Particulier", "Entreprise"],
      document: {
        email: "",
        role: "",
        permissions: "",
        //
        first_name: "",
        last_name: "",
        state: "",
        city: "",
        mobile: "",
        kind: "",
        created_at: "",
        modified_at: ""
      },
      exists: false,
      errorMessage: null,
      isLoading: false
    };
  },
  computed: {
    cities_filtred() {
      let cities_filtred = [];
      if (this.document.state == "") return cities_filtred;
      let state = this.states.find(state => state.value == this.document.state);
      if (state) cities_filtred = [{ value: "", text: "" }, ...state.cities];
      return cities_filtred;
    },
    currentUser() {
      return this.$store.state.user;
    }
  },
  watch: {
    state(newVal) {
      if (newVal == this.initState) this.city = this.initCity;
      else this.document.city = "";
    },
    currentUser(newVal) {
      if (newVal) this.load_profile(newVal);
    }
  },
  created() {
    this.states = algeria_cities;
    if (this.currentUser) this.load_profile(this.currentUser);
  },
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
    },
    load_profile(currentUser) {
      if (!currentUser) {
        console.log("No user is available");
        return;
      }
      console.log("load_profile", currentUser.uid);
      //
      this.document.uid = currentUser.uid;
      //
      this.isLoading = true;
      this.errorMessage = null;
      db.collection("users")
        .doc(currentUser.uid)
        .get()
        .then(doc => {
          this.isLoading = false;
          this.errorMessage = null;
          if (doc.exists) {
            console.log("Document successfully loaded!");
            this.document.uid = currentUser.uid || "";
            this.document.email = doc.data().email || "";
            this.document.role = doc.data().role || "DRIVER";
            this.document.permissions = doc.data().permissions || "";
            //
            this.document.first_name = doc.data().first_name || "Mohammed";
            this.document.last_name = doc.data().last_name || "HAMIMED";
            this.document.state = doc.data().state || "Mila";
            this.document.city = doc.data().city || "Sidi Merouane";
            this.document.mobile = doc.data().mobile || "0793470277";
            this.document.vehicle = doc.data().vehicle || "Car";
            this.document.kind = doc.data().kind || "Particulier";
            this.document.created_at = doc.data().created_at || "";
            this.document.modified_at = doc.data().modified_at || "";
          } else {
            this.document.uid = currentUser.uid;
            this.document.email = currentUser.email;
            this.document.role = "DRIVER";
            this.document.permissions = "";
            //
            this.document.first_name = "Mohammed";
            this.document.last_name = "HAMIMED";
            this.document.state = "Mila";
            this.document.city = "Sidi Merouane";
            this.document.mobile = "0793470277";
            this.document.vehicle = "Car";
            this.document.kind = "Particulier";
            this.document.created_at = "";
            this.document.modified_at = "";
            console.log("Document could not be found!");
          }
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log(error);
        });
    },
    formSubmit() {
      this.set_profile(this.document.uid);
    },
    set_profile(uid = null) {
      this.isLoading = true;
      this.errorMessage = null;
      let date = FF_FieldValue.serverTimestamp();
      let document = {
        uid: uid,
        email: this.document.email,
        role: this.document.role,
        permissions: this.document.permissions,
        //
        first_name: this.document.first_name,
        last_name: this.document.last_name,
        state: this.document.state,
        city: this.document.city,
        mobile: this.document.mobile,
        vehicle: this.document.vehicle,
        kind: this.document.kind,
        created_at: date,
        modified_at: date
      };
      db.collection("users")
        .doc(uid)
        .set(document, { merge: true })
        .then(() => {
          this.isLoading = false;
          this.errorMessage = null;
          console.log("Document successfully created!");
          this.$router.push({
            name: "home"
          });
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log("error", error);
        });
    }
  }
};
</script>

